<template>
  <main class="agency__management__main">
    <AgencyPopup
      :popupEventFlag="popupEventFlag"
      @popupClose="popupToggle"
      :popupState="popupState"
      :selectedObj="selectedRowObj"
      @refreshList="getOrgns"
    />
    <section class="box__shadow__01 vertical__scroll grid__wrap">
      <h3 class="page__title">
        {{ this.$store.state.pageInfo["기관 관리"].title }}
      </h3>
      <h5 class="page__sub__title f__regular">
        {{ this.$store.state.pageInfo["기관 관리"].subTitle }}
      </h5>
      <div class="add__or__search__wrap">
        <ul>
          <li>
            <dl>
              <dt>사용여부</dt>
              <dd>
                <select
                  name=""
                  id=""
                  ref="useYnSelector"
                  v-model="useYnFilter"
                  @change="gridDataFiltering"
                >
                  <option value="">전체</option>
                  <option value="사용">사용</option>
                  <option value="미사용">미사용</option>
                </select>
              </dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt>기관</dt>
              <dd>
                <input
                  type="text"
                  class="custom__input"
                  placeholder="이름을 입력하세요"
                  v-model="orgnNameFilter"
                />
              </dd>
            </dl>
          </li>
          <li>
            <button class="custom__btn" @click="gridDataFiltering">
              <span>
                <img
                  src="@/assets/images/icon/ic-search.svg"
                  alt="검색 아이콘"
                />
              </span>
              <p>검색</p>
            </button>
          </li>
        </ul>
        <button class="custom__btn" @click="popupToggle('추가')">
          <span>
            <img src="@/assets/images/icon/ic-plus.svg" alt="추가 아이콘" />
          </span>
          <p>추가</p>
        </button>
      </div>
      <div class="grid__wrap" @click="rowClick($event)">
        <Grid
          class="on"
          ref="gridEl"
          :cols="cols"
          :pagination="true"
          :sort="true"
          :rows="filteredGridData"
        ></Grid>
      </div>
    </section>
  </main>
</template>
<script>
import "@/assets/css/agencyManagement.css";
import { Grid } from "gridjs-vue";
import AgencyPopup from "@/components/SystemManagement/AgencyManagement/AgencyPopup";
import { getOrgnList } from "@/api/orgn";

export default {
  components: {
    Grid,
    AgencyPopup,
  },
  data() {
    return {
      popupEventFlag: "Close",
      cols: [
        { name: "번호", id: "index" },
        { name: "기관명", id: "orgnNm" },
        { name: "대표자", id: "ceoNm" },
        { name: "등록번호", id: "regNumber" },
        { name: "대표전화", id: "orgnNumber" },
        { name: "담당자명", id: "managerName" },
        { name: "Email", id: "managerEmail" },
        { name: "팩스번호", id: "orgnFaxNo" },
        { name: "등록자", id: "regNm" },
        { name: "사용여부", id: "useYn" },
        { name: "등록일시", id: "regDttm" },
      ],
      orgnList: [],
      useYnFilter: "사용",
      orgnNameFilter: "",
      selectedRowObj: "",
      popupState: "",
      filteredGridDataBack: [],
      filteredGridData: [],
    };
  },
  created() {
    this.getOrgns();
  },
  methods: {
    popupToggle(evtName) {
      if (evtName === "추가") this.selectedRowObj = false;
      this.popupState = evtName;
      this.popupEventFlag === "Close"
        ? (this.popupEventFlag = "Open")
        : (this.popupEventFlag = "Close");
    },
    rowClick(param) {
      const selectedObjIndex = Number(param.path[1].childNodes[0].textContent);
      const selectedCell = param.path[0].getAttribute("data-column-id");
      if (selectedCell === "orgnNm") this.popupToggle("수정");
      this.selectedRowObj = { ...this.getOrgnList[selectedObjIndex - 1] };
    },
    getOrgns() {
      /*let filter = null;
      if (this.useYnFilter !== "" || this.orgnNameFilter !== "") {
        filter = {
          orgnName: this.orgnNameFilter,
          useYn: this.useYnFilter,
        };
      }*/
      getOrgnList().then((response) => {
        this.orgnList = response.data.response.data;
        this.orgnList.forEach(function (object, index) {
          object.index = index + 1;
          if (object.useYn === "Y") {
            object.useYn = "사용";
          } else {
            object.useYn = "미사용";
          }
        });
        this.filteredGridDataBack = this.orgnList;
        if (this.orgnNameFilter === "" && this.useYnFilter === "사용") {
          this.filteredGridData = this.orgnList;
          this.filteredGridData = this.filteredGridData.filter(
            (obj) => obj.useYn.trim() === "사용"
          );
        } else {
          this.gridDataFiltering();
        }
      });
    },
    gridDataFiltering() {
      this.filteredGridData = this.filteredGridDataBack;

      if (this.useYnFilter !== "") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.useYn.trim() === this.useYnFilter
        );
      }
      if (this.filteredGridData.length > 0) {
        const nameFilter = [];
        this.filteredGridData.forEach((obj) => {
          if (obj.orgnNm.includes(this.orgnNameFilter)) {
            nameFilter.push(obj);
          }
        });
        this.filteredGridData = nameFilter;
      }
    },
  },
  computed: {
    getOrgnList() {
      return this.orgnList;
    },
  },
};
</script>
