<template>
  <div class="popup__wrap" :class="{ on: popupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup box__shadow__02">
      <div class="popup__top">
        <h4>기관 {{ popupState }}</h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <ul class="vertical__scroll">
        <li>
          <dl>
            <dt class="f__medium">기관 아이디</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqOrgnInfo.orgnId"
                :disabled="popupState === '수정'"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">기관명</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqOrgnInfo.orgnNm"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">대표자명</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqOrgnInfo.ceoNm"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">사업자등록번호</dt>
            <dd>
              <input
                type="phone"
                class="custom__input"
                v-model="reqOrgnInfo.regNumber"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">전화번호</dt>
            <dd>
              <input
                type="tel"
                class="custom__input"
                v-model="reqOrgnInfo.orgnNumber"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">FAX</dt>
            <dd>
              <input
                type="tel"
                class="custom__input"
                v-model="reqOrgnInfo.orgnFaxNo"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">담당자명</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqOrgnInfo.managerName"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">Email</dt>
            <dd>
              <input
                type="email"
                class="custom__input"
                v-model="reqOrgnInfo.managerEmail"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">우편번호</dt>
            <dd class="zip__code__line">
              <ul>
                <li>
                  <input
                    type="text"
                    class="custom__input read__only"
                    readonly
                    ref="zipCodeInput"
                    v-model="reqOrgnInfo.zipCode"
                  />
                </li>
                <li>
                  <button class="custom__btn" @click="zipcodeSearch">
                    <span>
                      <img
                        src="@/assets/images/icon/ic-search.svg"
                        alt="저장 아이콘"
                      />
                    </span>
                    <p>찾기</p>
                  </button>
                </li>
              </ul>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">주소</dt>
            <dd>
              <input
                type="text"
                ref="addressInput"
                class="custom__input read__only"
                v-model="reqOrgnInfo.address"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">상세주소</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqOrgnInfo.addressDtl"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">사용여부</dt>
            <dd>
              <select
                name=""
                id=""
                ref="useYnSelector"
                v-model="reqOrgnInfo.useYn"
              >
                <option value="">선택</option>
                <option value="Y">사용</option>
                <option value="N">미사용</option>
              </select>
            </dd>
          </dl>
        </li>
      </ul>
      <div class="custom__btn__wrap">
        <button class="custom__btn delete" @click="removeOrClean">
          <span>
            <img src="@/assets/images/icon/ic-remove.svg" alt="저장 아이콘" />
          </span>
          <p>삭제</p>
        </button>
        <button class="custom__btn" @click="addOrgn">
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>저장</p>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { addOrgn, updateOrgn, removeOrgn } from "@/api/orgn";
import { clear } from "@/utill/commom";

export default {
  props: {
    popupEventFlag: String,
    popupState: String,
    selectedObj: [Object, Boolean],
  },
  watch: {
    popupState() {
      if (this.popupState === "수정") {
        this.reqOrgnInfo = this.selectedObj;
        if (this.selectedObj.useYn === "사용") this.reqOrgnInfo.useYn = "Y";
        else if (this.selectedObj.useYn === "미사용")
          this.reqOrgnInfo.useYn = "N";
      } else if (this.popupState === "추가") {
        this.reqOrgnInfo = {};
      }
    },
  },
  data() {
    return {
      reqOrgnInfo: {
        address: "",
        addressDtl: "",
        ceoNm: "",
        managerEmail: "",
        managerName: "",
        orgnFaxNo: "",
        orgnId: "",
        orgnNm: "",
        orgnNumber: "",
        regNumber: "",
        useYn: "",
        zipCode: "",
      },
    };
  },
  methods: {
    popupCloseClick() {
      this.$emit("popupClose", "Close");
    },
    zipcodeSearch() {
      const obj = this.reqOrgnInfo;
      new window.daum.Postcode({
        oncomplete: function (data) {
          obj.zipCode = data.zonecode;
          obj.address = data.address;
        },
      }).open();
    },
    addOrgn() {
      if (this.validateOrgn()) {
        if (this.$env == "development") console.log(this.reqOrgnInfo);
        if (this.popupState === "수정") {
          updateOrgn(this.reqOrgnInfo)
            .then((response) => {
              if (response.data.status === "OK") {
                this.$toast.success("기관정보 수정이 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        } else {
          addOrgn(this.reqOrgnInfo)
            .then((response) => {
              if (this.$env == "development") console.log(response);
              if (response.data.status === "OK") {
                this.$toast.success("기관정보 추가가 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        }
      }
    },
    validateOrgn() {
      if (this.reqOrgnInfo.orgnId === "" || this.reqOrgnInfo.orgnId === null) {
        this.$toast.error("기관 아이다를 선택하세요.");
        return false;
      }
      if (this.reqOrgnInfo.orgnNm === "" || this.reqOrgnInfo.orgnNm === null) {
        this.$toast.error("기관 명을 입력하세요.");
        return false;
      }
      if (this.reqOrgnInfo.ceoNm === "" || this.reqOrgnInfo.ceoNm === null) {
        this.$toast.error("대표자 명을 입력하세요.");
        return false;
      }
      if (
        this.reqOrgnInfo.regNumber === "" ||
        this.reqOrgnInfo.regNumber === null
      ) {
        this.$toast.error("사업자등록번호를 입력하세요.");
        return false;
      }
      if (
        this.reqOrgnInfo.orgnNumber === "" ||
        this.reqOrgnInfo.orgnNumber === null
      ) {
        this.$toast.error("전화번호를 입력하세요.");
        return false;
      }
      if (
        this.reqOrgnInfo.orgnFaxNo === "" ||
        this.reqOrgnInfo.orgnFaxNo === null
      ) {
        this.$toast.error("FAX번호를 입력하세요.");
        return false;
      }
      if (
        this.reqOrgnInfo.managerName === "" ||
        this.reqOrgnInfo.managerName === null
      ) {
        this.$toast.error("담당자 명을 입력하세요.");
        return false;
      }
      if (
        this.reqOrgnInfo.managerEmail === "" ||
        this.reqOrgnInfo.managerEmail === null
      ) {
        this.$toast.error("Email를 입력하세요.");
        return false;
      }

      if (this.reqOrgnInfo.useYn === "" || this.reqOrgnInfo.useYn === null) {
        this.$toast.error("사용여부를 입력하세요.");
        return false;
      }
      return true;
    },
    removeOrClean() {
      if (this.popupState === "수정") {
        removeOrgn(this.reqOrgnInfo.orgnId)
          .then((response) => {
            if (this.$env == "development") console.log(response);
            if (response.data.status === "OK") {
              this.$toast.success("기관정보가 삭제되었습니다");
              this.popupCloseClick();
              this.$emit("refreshList");
            }
          })
          .catch((error) => this.$toast.error(error.message));
      } else if (this.popupState === "추가") {
        clear(this.reqOrgnInfo);
      }
    },
  },
};
</script>
